import { useTranslation } from 'react-i18next';
import './contact.scss';
import React from 'react';

function Contact() {
    const { t } = useTranslation();
    return (
        <div className='wrapper'>
            <div className='container'>
                <div className='block-contact'>
                    <div className='contact-tittle'>
                        <p className='item animation_item hide'>{t("contact")}</p>
                    </div>
                        <div className="contact-info">
                            <form method='POST' action='../../public/sendmail.php'>
                                <div className="contact-about fs-4">
                                    <p className='mt-3 fw-bolder item-name animation_item hide'>{t("name")}<br></br>
                                        <label htmlFor='formName'></label>
                                        <input type="text" id='formName' name='name' className="line" placeholder= {t("your_name")}></input>
                                    </p>
                                    <p className='mt-3 fw-bolder item-mail animation_item hide'>{t("email")}<br></br>
                                        <label htmlFor='formMail'></label>
                                        <input type="textl" id='formMail' name='email' className="line" placeholder= {t("your_mail")}></input>
                                    </p>
                                </div>
                                <div className="contact-textarea fs-4 mt-5 fw-bolder">
                                    <p className='item animation_item hide msg'>{t("message")}</p>
                                    <label htmlFor='formText'></label>
                                    <textarea id='formText' name='message' className="line item animation_item hide" placeholder={t("your_message")}></textarea>
                                </div>
                                <div className="contact-send mt-5">
                                    <button type="submit" value="Send Email" className='fw-bold animation_item hide'>{t("submit")}</button>
                                </div>
                            </form>
                        </div>
                </div>
            </div>
        </div>
    );     
}
export default Contact;